export default {
	methods: {
		getColor(name) {
			const colors = [
				'#F46A5F', '#FC8256', '#FFC33D', '#49BEB7',
				'#4C96D7', '#8555D7', '#FF7DCC', '#D4C5F9',
				'#4992E6', '#FF6B8A', '#01A9DB', '#B6FCD5'
			];

			let hash = 0;
			for (let i = 0; i < name.length; i++) {
				hash = name.charCodeAt(i) + ((hash << 5) - hash);
			}

			const index = Math.abs(hash) % colors.length;
			return colors[index];
		},
		getFirstLetter(str) {
			if (str && str.length > 0) {
				return str.charAt(0);
			}
			return '';
		}
	},
};