<template>
	<div class="center">
		<div class="button__inner">
			<button-vue
				class="button"
				:color="'white'"
				@click="$router.push('/new-template')"
			>
				Создать
			</button-vue>
		</div>
		<div class="container">
			<div class="wrapper">
				<div 
					v-if="!templatesOrigin.length"
					class="empty_list"
				>
					Список пуст
				</div>
				<div class="search-bar">
					<BaseSearchBar 
						:data-for-filter="templates"
						:search-key="'template_name'"
						@onFilter="onFilterHandle"
					/>
				</div>
				<v-virtual-scroll
					:items="templatesOrigin"
					:item-height="70"
					height="800px"
				>
					<template #default="{ item }">
						<div class="list_item">
							<div class="list_item__block_info">
								<v-avatar :color="getColor(item.template_name)">
									<div>{{ getFirstLetter(item.template_name) }}</div>
								</v-avatar>
								<button
									class="block_info__name"
									@click="openModal(item)"
								>
									{{ item.template_name }}
								</button>
							</div>
							<div class="list_item__additional d-flex">
								<div class="list_item__info">
									<span class="list_item__time">{{ dayjs(item.created_at).format('DD.MM.YYYY hh:mm:ss') }}</span>
									<pre>{{ item.status }}</pre>
								</div>
								<div>
									<v-btn
										elevation="2"
										icon
										plain
										@click="deleteTemplate(item)"
									>
										<v-icon color="red">
											mdi-trash-can
										</v-icon>
									</v-btn>
								</div>
							</div>
						</div>
					</template>
				</v-virtual-scroll>
				<BaseModal
					:open="open"
					title="Детали шаблона"
					@onModalClose="onCloseModalHandle"
				>
					<template slot="content">
						<div class="pa-5">
							<div>
								<h3>Данные шаблона</h3>
								<div>
									<div>
										<span>Тип шаблона:</span>
										<span>{{ selectedItem?.edna_shortcut?.messageMatcher?.type }}</span>
									</div>
									<div>
										<span>Название шаблона:</span>
										<span>{{ selectedItem?.template_name }}</span>
									</div>
									<div>
										<span>Категория шаблона:</span>
										<span>{{ selectedItem?.edna_shortcut?.messageMatcher?.category }}</span>
									</div>
									<div>
										<span>Язык шаблона:</span>
										<span>{{ selectedItem?.edna_shortcut?.messageMatcher?.language }}</span>
									</div>
								</div>
							</div>
							<v-divider />
							<div>
								<h3>Заголовок</h3>
								<div v-if="selectedItem?.edna_shortcut?.messageMatcher?.content?.header?.headerType === 'TEXT'">
									<span>{{ selectedItem?.edna_shortcut?.messageMatcher?.content?.header?.headerType }}:</span>
									<span>{{ selectedItem?.edna_shortcut?.messageMatcher?.content?.header?.text }}</span>
								</div>
							</div>
							<v-divider />
							<div>
								<h3>Текст шаблона</h3>
								<div>
									<p>Текст и порядок строк символов в шаблоне регистрируются у оператора как пример исходящего сообщения. Подставляйте конкретные переменные или необходимый текст вместо строк символов в пользовательском шаблоне и используйте его для рассылок вашим клиентам.</p>
								</div>
								<span>{{ selectedItem?.edna_shortcut?.messageMatcher?.content?.header?.text }}</span>
							</div>
							<v-divider />
							<div class="mt-4">
								<span>Время создания:</span>
								<span>{{ dayjs(selectedItem.timestamp).format('DD.MM.YYYY hh:mm:ss') }}</span>
							</div>
						</div>
					</template>
				</BaseModal>
				<vue-element-loading 
					:active="isLoading"
					:background-color="'rgba(255, 255, 255, 1)'"
					:color="'blue'"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import shepherdMixin from '@/mixins/shepherdMixin';
import { mapGetters } from 'vuex';
import BaseSearchBar from '@/components/BaseSearchBar.vue';
import BaseModal from '@/components/BaseModal.vue';
import ButtonVue from '@/components/BaseButton.vue';
import { HTTP } from '@/helpers/httpCommon';
import dayjs from 'dayjs';
import avatarGenerateMixin from '@/mixins/avatarGenerateMixin';
export default {
	components: {
		BaseModal,
		BaseSearchBar,
		ButtonVue
	},
	mixins: [shepherdMixin, avatarGenerateMixin],
	data: () => ({
		isLoading: false,
		open: false,
		selectedItem: {},
		templatesOrigin: [],
	}),
	computed: {
		...mapGetters(['getTemplates']),
		templates() {
			return this.getTemplates;
		}
	},
	watch: {
		templates(newTemplates) {
			this.templatesOrigin = [...newTemplates];
		}
	},
	created() {
		this.getData();
	},
	methods: {
		getData(id = this.$route.params.userId) {
			this.isLoading = false;
			const body = {};
			
			if(id) body.username = id;

			HTTP.get('get/messages/matcher')
				.then(response => {
					const data = response.data;
					const filtredData = data.sort((a, b) => {
						return new Date(b.date) - new Date(a.date);
					});
					this.$store.commit('setTemplate', filtredData.reverse());
				})
				.catch(error => {
					this.$toast.error(error.message, {
						timeout: 2000
					});
				}).finally(() => {
					this.isLoading = false;
				});

		},
		dayjs: dayjs,
		onCloseModalHandle() {
			this.selectedItem = {};
			this.open = false;
		},
		onFilterHandle(results) {
			this.templatesOrigin = results;
		},
		openModal(item) {
			this.selectedItem = item;
			this.open = true;
		},
		deleteTemplate(item) {
			HTTP.delete(`get/messages/matcher/${item.edna_template_id}`)
				.then(() => {
					this.$toast.info('Шаблон успешно удален');
					this.templatesOrigin = this.templatesOrigin.filter(template => template.edna_template_id !== item.edna_template_id);
				})
				.catch(error => {
					this.$toast.error(error.message, {
						timeout: 2000
					});
				});
		}
	},
};
</script>

<style lang="scss" scoped>
	.button__inner {
		display: flex;
		justify-content: flex-end;
		max-width: 90vw;
		width: 100%;
		margin: 0 auto;
		margin-bottom: 15px;
	}
	.button {
		margin-left: auto;
	}
	.center {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 90vh;
		width: 100%;
	}
	.container {
		border-radius: 20px;
		background-color: white;
		max-width: 90vw;
		width: 100%;
		max-height: 80vh;
		height: 100%;
		overflow: hidden;
	}
	.empty_list {
		position: absolute;
		top: 60%;
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
		width: 115px;
	}
	.wrapper {
		position: relative;

		padding-top: 10px;
		padding-left: 10px;
		padding-bottom: 19px;
		padding-right: 0;
	}
	.search-bar {
		margin: 0 30px 0 10px;
	}
	.list_item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-right: 10px;
		height: 70px;
		.block_info {
			display: flex;
			&__picture_frame {
				display: flex;
				justify-content: center;
				align-items: center;
				
				width: 40px;
				height: 40px;

				border-radius: 50%;

				background-color: gray;
				color: white;
			}
			&__name {
				width: 20rem;
				margin-left: 15px;
				text-align: left;
				align-self: center;
			}
		}
		&__block_info {
			display: flex;
		}
		&__block_buttons {
			display: flex;
		}
	}

	.content_file {
		min-width: 50%;
		font-size: 13px;
	}

	.red_font {
		color: red;
	}
	.list_item__info {
		width: 200px;
	}
	.list_item__time {
		width: fit-content;
	}
</style>