<template>
	<v-text-field
		prepend-icon="fa-solid fa-magnifying-glass"
		placeholder="Search..."
		@input="search" 
	/>
</template>

<script>
export default {
	props: {
		dataForFilter: {
			type: Array,
			default: () => []
		},
		searchKey: {
			type: String,
			default: ''
		},
	},
	data() {
		return {
			filtredData: []
		};
	},
	methods: {
		search(searchQuery) {
			this.filtredData = this.dataForFilter.filter((item) =>
				item[this.searchKey].includes(searchQuery)
			);
			this.$emit('onFilter', this.filtredData);
		}
	},
};
</script>